.polka-dot {
    width: 240px;
    height: 240px;
    background-color: #000;
    background-image: radial-gradient(#fff 10%, transparent 11%),
      radial-gradient(#fff 10%, transparent 11%);
    background-size: 60px 60px;
    background-position: 0 0, 30px 30px;
    background-repeat: repeat;
  }

  .path.react-flow__edge-path:hover {
    stroke-width: 4;
  }
  .react-flow__edge-path-selector:hover { cursor: pointer }
.react-flow__edge-path-selector:hover + .react-flow__edge-path, .react-flow__edge-path:hover  {
    stroke: #555;
    cursor: pointer;
}
.react-flow__edge-path-selector {
    fill: none;
    stroke: transparent;
    stroke-width: 28;
}

.diagram-component {
  width: 1024px;
  height: 720px;
  border: 1px solid blueviolet;
}